<template>
  <el-form-item
    ref="formItemRef"
    class="liv-form-item"
    v-bind="formItemAttrs"
    :rules="formItemRules">
    <template #label>
      <div class="liv-form-item__label">
        <span>{{ formItemAttrs.label }}</span>
        <template v-if="contentAttrs.tooltip">
          <el-tooltip
            effect="dark"
            :content="contentAttrs.tooltip"
            :placement="contentAttrs.placement ?? 'top'">
            <el-icon class="liv-form-item__label-icon" :size="14">
              <QuestionFilled />
            </el-icon>
          </el-tooltip>
        </template>
      </div>
    </template>
    <el-input
      v-if="fieldType === 'input' || fieldType === 'textarea'"
      class="liv-form-item__input"
      :placeholder="'请输入' + (formItemAttrs.label ?? '')"
      :showWordLimit="contentAttrs.maxlength ? true : false"
      :type="fieldType === 'textarea' ? 'textarea' : 'text'"
      :rows="3"
      v-bind="omit(contentAttrs, ['prefix', 'suffix', 'prepend', 'append'])"
      v-model="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
      <template #prefix v-if="contentAttrs.prefix">
        {{ contentAttrs.prefix }}
      </template>
      <template #suffix v-if="contentAttrs.suffix">
        {{ contentAttrs.suffix }}
      </template>
      <template #prepend v-if="contentAttrs.prepend">
        {{ contentAttrs.prepend }}
      </template>
      <template #append v-if="contentAttrs.append">
        {{ contentAttrs.append }}
      </template>
    </el-input>
    <el-input
      v-else-if="fieldType === 'ciphertext'"
      class="liv-form-item__ciphertext"
      :placeholder="'请输入' + (formItemAttrs.label ?? '')"
      :showWordLimit="contentAttrs.maxlength ? true : false"
      :disabled="!ciphertextDecrypted"
      v-bind="omit(contentAttrs, ['type'])"
      :modelValue="model"
      @update:modelValue="ciphertextUpdate">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
      <template
        v-if="
          !$slots.append && initialVal !== undefined && initialVal !== null && initialVal !== ''
        "
        #append>
        <el-icon
          ><View v-if="ciphertextDecrypted" @click="resetCiphertext" /><Hide
            v-else
            @click="verifyVisible = true" />
        </el-icon>
      </template>
    </el-input>
    <liv-select
      v-else-if="fieldType === 'select'"
      class="liv-form-item__select"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </liv-select>
    <liv-cascader
      v-else-if="fieldType === 'cascader'"
      class="liv-form-item__cascader"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model="model"
      @change="validateFormItem">
      <template v-for="(_, name) in slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </liv-cascader>
    <liv-radio
      v-else-if="fieldType === 'radio'"
      class="liv-form-item__radio"
      v-bind="contentAttrs"
      v-model="model">
      <template v-for="(_, name) in slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </liv-radio>
    <liv-checkbox
      v-else-if="fieldType === 'checkbox'"
      class="liv-form-item__checkbox"
      v-bind="contentAttrs"
      v-model="model"
      @change="validateFormItem">
      <template v-for="(_, name) in slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </liv-checkbox>
    <el-date-picker
      v-else-if="fieldType === 'date'"
      class="liv-form-item__date"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      :value-format="dateValueFormat[contentAttrs.type]"
      clearable
      v-bind="contentAttrs"
      v-model="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </el-date-picker>
    <el-time-picker
      v-else-if="fieldType === 'time'"
      class="liv-form-item__time"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model="model">
    </el-time-picker>
    <el-switch
      v-else-if="fieldType === 'switch'"
      class="liv-form-item__switch"
      v-bind="contentAttrs"
      v-model="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </el-switch>
    <el-input-number
      v-else-if="fieldType === 'number'"
      class="liv-form-item__number"
      :placeholder="'请输入' + (formItemAttrs.label ?? '')"
      v-bind="omit(contentAttrs, ['prefix', 'suffix'])"
      v-model="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
      <template #prefix v-if="contentAttrs.prefix">
        {{ contentAttrs.prefix }}
      </template>
      <template #suffix v-if="contentAttrs.suffix">
        {{ contentAttrs.suffix }}
      </template>
    </el-input-number>
    <number-range
      v-else-if="fieldType === 'numberRange'"
      class="liv-form-item__number-range"
      v-model="model"
      v-bind="contentAttrs">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </number-range>
    <el-rate v-else-if="fieldType === 'rate'" v-bind="contentAttrs" v-model="model" />
    <el-color-picker v-else-if="fieldType === 'color'" v-bind="contentAttrs" v-model="model" />
    <el-upload
      class="liv-form-item__upload"
      v-else-if="
        fieldType === 'image' ||
        fieldType === 'audio' ||
        fieldType === 'video' ||
        fieldType === 'file'
      "
      :action="contentAttrs['chunk'] ? '' : fileUploadUrl"
      :http-request="contentAttrs['chunk'] ? fileHttpRequest : undefined"
      :headers="typeof fileUploadHeader === 'function' ? fileUploadHeader() : fileUploadHeader"
      :list-type="uploadListType[fieldType]"
      :data="typeof fileUploadData === 'function' ? fileUploadData() : fileUploadData"
      :accept="uploadAccpet[fieldType]"
      :limit="9"
      :on-preview="onPreview"
      :class="{
        'liv-form-item__upload--hide': model.length >= (contentAttrs.limit || 9),
      }"
      v-bind="omit(contentAttrs, ['on-success', 'onSuccess', 'on-remove', 'onRemove'])"
      :file-list="model"
      :on-success="uploadSuccess"
      :on-remove="uploadRemove"
      :before-upload="beforeUpload">
      <el-icon
        v-if="
          (contentAttrs['listType'] ?? contentAttrs['list-type'] ?? uploadListType[fieldType]) ===
          'picture-card'
        ">
        <Plus />
      </el-icon>
      <el-button
        v-else
        type="primary"
        plain
        :disabled="((model as [])?.length ?? 0) >= ((contentAttrs.limit as number) || 9)">
        <el-icon style="margin-right: 4px" size="16"><FolderOpened /></el-icon>
        选择文件</el-button
      >
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
      <template v-if="contentAttrs.tip" #tip>
        <div class="liv-form-item__upload--tip">{{ contentAttrs.tip }}</div>
      </template>
    </el-upload>
    <dict-select
      v-else-if="fieldType === 'dict'"
      class="liv-form-item__dict"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </dict-select>
    <dict-select
      v-else-if="fieldType === 'dictId'"
      class="liv-form-item__dict"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model:dict-id="model">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </dict-select>
    <grid-cascader
      v-else-if="fieldType === 'grid' || fieldType === 'gridCode'"
      class="liv-form-item__grid"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model="model"
      @change="validateFormItem">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </grid-cascader>
    <grid-cascader
      v-else-if="fieldType === 'gridId'"
      class="liv-form-item__grid"
      :placeholder="'请选择' + (formItemAttrs.label ?? '')"
      clearable
      v-bind="contentAttrs"
      v-model:grid-id="model"
      @change="validateFormItem">
      <template v-for="(_, name) in $slots" #[name]="slotData">
        <slot :name="name" v-bind="slotData || {}" />
      </template>
    </grid-cascader>
    <div class="liv-form-item__location-picker" v-else-if="fieldType === 'location'">
      <el-button type="primary" plain @click="locationPickerDialog = true">
        <el-icon style="margin-right: 4px" size="16"><Location /></el-icon>
        选择位置</el-button
      >
      <div class="liv-form-item__location-picker--value">
        <el-tooltip placement="top" v-if="model?.[0] || model?.[1]">
          <span v-if="address">{{ address }}</span>
          <span v-else-if="model?.[0] || model?.[1]">
            经度：{{ model?.[0] }} 纬度：{{ model?.[1] }}
          </span>
          <template #content>
            <div v-if="address">{{ address }}</div>
            <div>经度：{{ model?.[0] }} 纬度：{{ model?.[1] }}</div>
          </template>
        </el-tooltip>
      </div>
    </div>
    <div v-else-if="fieldType === 'richText'">
      <rich-text class="liv-form-item__rich-text" v-model="model" v-bind="contentAttrs"></rich-text>
    </div>
    <template v-else-if="fieldType === 'slot' && formItemAttrs.prop">
      <slot :name="formItemAttrs.prop"></slot>
    </template>
    <template v-else>
      <slot></slot>
    </template>
    <slot name="right"></slot>
  </el-form-item>
  <el-image-viewer
    @close="() => (imageViewerVisible = false)"
    v-if="imageViewerVisible"
    :url-list="previewList" />
  <el-dialog title="选取经纬度" v-model="locationPickerDialog" width="55%">
    <location-picker
      v-bind="contentAttrs"
      v-model="model"
      v-model:address="address"
      @confirm="locationPickerConfirm()"></location-picker>
  </el-dialog>
  <verify
    v-if="fieldType === 'ciphertext'"
    v-bind="contentAttrs"
    v-model="verifyVisible"
    @success="verifySuccess"></verify>
</template>

<script lang="ts" setup>
import {
  type Ref,
  type ShallowRef,
  computed,
  inject,
  nextTick,
  onMounted,
  ref,
  shallowRef,
  useAttrs,
  useSlots,
  watch,
} from 'vue';
import {
  ElFormItem,
  ElInput,
  ElIcon,
  ElDialog,
  ElDatePicker,
  ElTimePicker,
  ElSwitch,
  ElInputNumber,
  ElRate,
  ElColorPicker,
  ElUpload,
  ElButton,
  formItemProps as elFormItemProps,
  genFileId,
  formContextKey,
  ElMessage,
  ElImageViewer,
  ElTooltip,
} from 'element-plus';
import type {
  FormItemProps as ElFormItemProps,
  FormItemInstance as ElFormItemInstance,
  FormItemRule,
  UploadFile as ElUploadFile,
  UploadFiles,
  FormContext,
  UploadRequestOptions,
  UploadProgressEvent,
  UploadRawFile,
} from 'element-plus';
import { FolderOpened, Plus, Location, View, Hide, QuestionFilled } from '@element-plus/icons-vue';
import { omit, pick } from 'lodash-es';
import { FormItemProps } from './form-item';
import LivSelect from '../../select/src/select.vue';
import LivRadio from '../../radio/src/radio.vue';
import LivCheckbox from '../../checkbox/src/checkbox.vue';
import LivCascader from '../../cascader/src/cascader.vue';
import DictSelect from '../../dict-select/src/dict-select.vue';
import GridCascader from '../../grid-cascader/src/grid-cascader.vue';
import LocationPicker from '../../location-picker/src/location-picker.vue';
import RichText from '../../rich-text/src/rich-text.vue';
import NumberRange from '../../number-range/src/number-range.vue';
import Verify from '../../verify/src/verify.vue';
import type { VerifyResult } from '../../verify';
import {
  getFileName,
  getMd5ByFile,
  injectHandleFileUrls,
  injectHandleFileResponse,
  injectFileUploadUrl,
  injectFileUploadHeader,
  injectFileUploadData,
  injectFileChunkUploadConfig,
  injectFileChunkUploadFunction,
  injectFileChunkMergeFunction,
  validateNonEmpty,
} from '@liv-web/utils';
import { clone } from 'lodash-es';
import {
  CIPHERTEXT_STATUS_KEY,
  DATA_FORM_AMAP_KEY,
  DATA_FORM_MODEL_KEY,
  FORM_ITEM_KEY,
} from '@liv-web/constants';
import type { Awaitable } from 'element-plus/es/utils';

/**
 * 组件配置
 */
defineOptions({
  name: 'LivFormItem',
  inheritAttrs: false,
});

/**
 * 部分组件透传插槽会报ts错误，暂时先用该方法解决
 */
const slots: {
  default?: any;
} = useSlots();

/**
 * 组件插槽
 */
defineSlots<{
  [key: string]: any;
}>();

/**
 * 注入文件上传地址
 */
const fileUploadUrl = injectFileUploadUrl();

/**
 * 注入文件切片上传配置数据
 */
const fileChunkUploadConfig = injectFileChunkUploadConfig();

/**
 * 注入文件上传地址
 */
const fileChunkUploadFunction = injectFileChunkUploadFunction();

/**
 * 注入文件上传地址
 */
const fileChunkMergeFunction = injectFileChunkMergeFunction();

/**
 * 注入文件上传请求Header
 */
const fileUploadHeader = injectFileUploadHeader();

/**
 * 注入文件上传请求Data
 */
const fileUploadData = injectFileUploadData();

/**
 * 注入处理文件上传返回response方法
 */
const handleFileResponse = injectHandleFileResponse();

/**
 * 注入处理文件上传地址方法
 */
const handleFileUrls = injectHandleFileUrls();

/**
 * 注入表单组件数据模型
 */
const formModel = inject<Ref<{ [key: string]: any }> | undefined>(DATA_FORM_MODEL_KEY, undefined);

/**
 * 注入AMap实例
 */
const AMapRef = inject<ShallowRef<any>>(DATA_FORM_AMAP_KEY, shallowRef());

/**
 * 表单项组件实例
 */
const formItemRef = shallowRef<ElFormItemInstance | null>(null);

/**
 * 全局属性
 */
const globalProps = inject(FORM_ITEM_KEY, {});

/**
 * 组件属性
 */
const props = defineProps<FormItemProps>();

/**
 * 组件attribute
 */
const attrs = useAttrs();

/**
 * form-item属性
 */
const formItemAttrs = computed<ElFormItemProps>(() => {
  return pick(
    Object.assign(globalProps ?? {}, attrs),
    Object.keys(elFormItemProps),
  ) as ElFormItemProps;
});

/**
 * 具体表单项实现组件属性
 */
const contentAttrs = computed<any>(() => {
  return omit(attrs, Object.keys(elFormItemProps));
});

/**
 * 表单组件上下文
 */
const formContext: FormContext | undefined = inject(formContextKey);

/**
 * 表单项初始值快照
 */
const initialVal = ref();

/**
 * 双向绑定model
 */
const [model, modifiers] = defineModel({
  default: undefined,
  type: [String, Number, Boolean, Object, Array],
  set(value) {
    if (modifiers.raw) {
      return value;
    } else {
      switch (props.fieldType) {
        case 'input':
        case 'ciphertext':
        case 'textarea':
        case 'radio':
        case 'date':
        case 'time':
        case 'number':
        case 'switch':
        case 'rate':
        case 'color':
        case 'richText':
        case 'numberRange':
        case 'location':
        case 'slot':
        case undefined:
          return value;
        case 'select':
        case 'dict':
        case 'dictId':
          if (attrs.multiple === '' || attrs.multiple === true) {
            return Array.isArray(value) ? value.join(',') : value;
          } else {
            return value;
          }
        case 'cascader':
        case 'grid':
        case 'gridCode':
        case 'gridId':
          if ((attrs.props as any)?.multiple === '' || (attrs.props as any)?.multiple === true) {
            return Array.isArray(value) ? value.join(',') : value;
          } else {
            return value;
          }
        case 'checkbox':
          return (value as []).join(',');
        case 'image':
        case 'audio':
        case 'video':
        case 'file':
          fileList.value = value as UploadFiles;
          return uploadFiles2Urls(value as UploadFiles);
        default:
          const n: never = props.fieldType;
          console.warn(`[liv-web/form-item]传入了一个未知的fieldType类型，fieldType=${n}。`);
          return value;
      }
    }
  },
  get(value) {
    if (modifiers.raw) {
      return value;
    } else {
      switch (props.fieldType) {
        case 'input':
        case 'ciphertext':
        case 'textarea':
        case 'radio':
        case 'time':
        case 'number':
        case 'switch':
        case 'rate':
        case 'color':
        case 'richText':
        case 'numberRange':
        case 'location':
        case 'slot':
        case undefined:
          return value;
        case 'date':
          return typeof value === 'number' ? value + '' : value;
        case 'select':
        case 'dict':
        case 'dictId':
          if (attrs.multiple === '' || attrs.multiple === true) {
            return value ? (Array.isArray(value) ? value : (value as string).split(',')) : [];
          } else {
            return value;
          }
        case 'cascader':
        case 'grid':
        case 'gridCode':
        case 'gridId':
          if ((attrs.props as any)?.multiple === '' || (attrs.props as any)?.multiple === true) {
            return value ? (Array.isArray(value) ? value : (value as string).split(',')) : [];
          } else {
            return value;
          }
        case 'checkbox':
          return value ? (value as string).split(',') : [];
        case 'image':
        case 'audio':
        case 'video':
        case 'file':
          return urls2UploadFiles(value as string);
        default:
          const n: never = props.fieldType;
          console.warn(`[liv-web/form-item]传入了一个未知的fieldType类型，fieldType=${n}。`);
          return value;
      }
    }
  },
});

/**
 * 初始化表单项默认的rules
 */
const formItemRules = computed(() => {
  const { required: requiredAttr, label, rules, prop } = formItemAttrs.value;
  const required = (requiredAttr as boolean | string | undefined) === '' || requiredAttr === true;
  const emptyRule: FormItemRule = {
    required: required,
    message: '',
    trigger: ['blur', 'change'],
    validator: validateNonEmpty,
  };
  if (props.fieldType === 'ciphertext' && ciphertextStatus?.value[prop as string] === false) {
    // 如果是加密表单项且未解密则跳过校验
    return undefined;
  }
  switch (props.fieldType) {
    case 'input':
    case 'textarea':
    case 'number':
    case 'richText':
    case 'ciphertext':
      emptyRule.message = `请输入${label ?? ''}`;
      break;
    case 'select':
    case 'cascader':
    case 'radio':
    case 'switch':
    case 'date':
    case 'time':
    case 'rate':
    case 'color':
    case 'dict':
    case 'dictId':
    case 'grid':
    case 'gridCode':
    case 'gridId':
      emptyRule.message = `请选择${label ?? ''}`;
      break;
    case 'checkbox':
      emptyRule.message = `请选择${label ?? ''}`;
      // 加了change页面会卡死，原因不明
      emptyRule.trigger = ['blur'];
      break;
    case 'image':
    case 'audio':
    case 'video':
    case 'file':
      emptyRule.message = `请上传${label ?? ''}`;
      break;
    case 'numberRange':
      emptyRule.message = `请输入${label ?? ''}`;
      emptyRule.validator = (_rule: any, _value: any, callback: any) => {
        if (
          model.value === undefined ||
          model.value === null ||
          model.value.length === 0 ||
          model.value?.[0] === undefined ||
          model.value?.[1] === undefined ||
          model.value?.[0] === null ||
          model.value?.[1] === null
        ) {
          callback(new Error());
        } else {
          callback();
        }
      };
      break;
    case 'location':
      emptyRule.message = `请选择${label ?? ''}`;
      emptyRule.validator = (_rule: any, _value: any, callback: any) => {
        if (
          model.value === undefined ||
          model.value === null ||
          model.value.length === 0 ||
          !model.value?.[0] ||
          !model.value?.[1]
        ) {
          callback(new Error());
        } else {
          callback();
        }
      };
      break;
    case 'slot':
    case undefined:
      emptyRule.message = `${label ?? '该项'}是必填项`;
      break;
    default:
      const n: never = props.fieldType;
      emptyRule.message = `${label ?? '该项'}是必填项`;
      console.warn(`[liv-web/form-item]传入了一个未知的fieldType类型，fieldType=${n}。`);
      break;
  }
  if (Array.isArray(rules)) {
    if (required) {
      return [emptyRule, ...rules];
    } else {
      return rules;
    }
  } else {
    if (rules) {
      if (required) {
        return [emptyRule, rules];
      } else {
        return rules;
      }
    } else {
      if (required) {
        return emptyRule;
      } else {
        return undefined;
      }
    }
  }
});

// 解决部分组件绑定值改变后没有触发校验的问题
function validateFormItem(_value?: any) {
  nextTick(() => formItemRef.value?.validate('change'));
}

/* el-date-picker */

/**
 * date picker组件value-format属性值
 */
const dateValueFormat: {
  [key: string]: string;
} = {
  undefined: 'YYYY-MM-DD',
  year: 'YYYY',
  month: 'YYYY-MM',
  date: 'YYYY-MM-DD',
  datetime: 'YYYY-MM-DD HH:mm:ss',
  dateTime: 'YYYY-MM-DD HH:mm:ss',
};

/* el-upload */

/**
 * el-upload组件回显类型
 */
const uploadListType: Record<
  'image' | 'audio' | 'video' | 'file',
  'text' | 'picture' | 'picture-card'
> = {
  image: 'picture-card',
  audio: 'text',
  video: 'text',
  file: 'text',
};

/**
 * el-upload组件接受文件类型
 */
const uploadAccpet = {
  image: 'image/*',
  audio: 'audio/*',
  video: 'video/*',
  file: '*',
};

/**
 * 是否显示图片预览组件
 */
const imageViewerVisible = ref(false);

/**
 * 预览图片集合
 */
const previewList = ref<string[]>([]);

/**
 * 上传组件的图片预览回调
 * @param uploadFile 需要预览的文件
 */
function onPreview(uploadFile: UploadFile) {
  if (props.fieldType === 'image') {
    previewList.value = [uploadFile.url!];
    imageViewerVisible.value = true;
  } else {
    ElMessage.warning('当前文件类型不支持预览！');
  }
}

/**
 * 上传文件列表
 */
const fileList = ref<UploadFiles>([]);

/**
 *  视频切片上传获取切片数据
 * @param file 视频文件
 * @param md5 文件唯一标识
 * @param chunkSize 切片大小
 * @returns
 * */
function getFileAllChunks(file: File, md5: string, name: string) {
  const chunkSize = fileChunkUploadConfig.chunkSize;
  const chunks = [];
  const fileSize = file.size;
  let count = Math.ceil(fileSize / chunkSize);
  let index = 0;
  const fileName = file.name;
  while (index < count) {
    const chunk = file.slice(index * chunkSize, (index + 1) * chunkSize);
    chunks.push({
      index,
      name,
      chunkSize,
      md5,
      fileSize,
      blob: chunk,
      fileName: fileName,
      totalChunkLength: count,
    });
    index++;
  }
  return chunks;
}

/**
 * el-upload组件切片上传请求方法
 * @param option el-upload的option
 */
async function fileHttpRequest(option: UploadRequestOptions): Promise<unknown> {
  const { file, onProgress } = option;

  const LIMIT = fileChunkUploadConfig.limit;
  // 获取文件唯一标识
  const md5 = await getMd5ByFile(file);
  const fileSize = file.size;

  const chunks = getFileAllChunks(file, md5, option.filename);
  const totalChunkLength = chunks.length;
  const progressArr = new Array(totalChunkLength).fill(0);
  try {
    while (chunks.length) {
      const requests = chunks.splice(0, LIMIT).map(async chunk => {
        await fileChunkUploadFunction?.(chunk, (progressEvent: { loaded: any }) => {
          progressArr[chunk.index] = progressEvent.loaded;
          const uploadedSize = progressArr.reduce((a, b) => a + b, 0);
          const progress = (uploadedSize / fileSize) * 100;
          onProgress?.({ percent: progress } as UploadProgressEvent);
        });
      });
      await Promise.all(requests);
    }

    const mergeParam = {
      md5,
      fileName: file.name,
      fileSize,
      totalChunkLength,
    };
    const res = await fileChunkMergeFunction?.(mergeParam);
    return Promise.resolve(res);
  } catch (error) {
    return Promise.reject(error);
  }
}

/**
 * el-upload文件上传成功回调
 * @param response 网格请求结果
 * @param uploadFile 上传成功的文件
 * @param uploadFiles 上传成功后的文件列表
 */
function uploadSuccess(response: any, uploadFile: UploadFile, uploadFiles: UploadFiles): void {
  if (uploadFiles.filter(uploadFile => uploadFile.status !== 'success').length === 0) {
    // 上传成功后更新model，如果是多选文件，等所有文件都上传成功后再更新model
    model.value = uploadFiles;
    // 校验表单
    validateFormItem(uploadFiles);
  }
  // 如果外部监听了onSuccess事件，则触发该事件
  const onSuccess = contentAttrs.value['on-success'] ?? contentAttrs.value['onSuccess'];
  if (onSuccess) {
    onSuccess(response, uploadFile, uploadFiles);
  }
}

/**
 * el-upload文件上传前回调
 * @param rawFile 上传文件对象
 */
function beforeUpload(
  rawFile: UploadRawFile,
): Awaitable<boolean | void | File | Blob | null | undefined> {
  if (props.fieldType === 'image' && !rawFile.type.startsWith('image')) {
    ElMessage.error('请上传图片类型文件!');
    return false;
  } else if (props.fieldType === 'audio' && !rawFile.type.startsWith('audio')) {
    ElMessage.error('请上传音频类型文件!');
    return false;
  } else if (props.fieldType === 'video' && !rawFile.type.startsWith('video')) {
    ElMessage.error('请上传视频类型文件!');
    return false;
  } else if (
    contentAttrs.value.extensions &&
    !contentAttrs.value.extensions.find((ext: string) => rawFile.name.endsWith(ext))
  ) {
    ElMessage.error(`只允许上传文件扩展名为${contentAttrs.value.extensions.join('、')}的文件！`);
    return false;
  } else if (contentAttrs.value.maxSize && rawFile.size > contentAttrs.value.maxSize) {
    ElMessage.error(
      `上传文件大小不能超过${contentAttrs.value.maxSize >= 1024 * 1024 ? contentAttrs.value.maxSize / 1024 / 1024 + 'M' : contentAttrs.value.maxSize / 1024 + 'KB'}！`,
    );
    return false;
  }
  // 如果外部监听了beforeUpload事件，则触发该事件
  const beforeUpload = contentAttrs.value['before-upload'] ?? contentAttrs.value['beforeUpload'];
  if (beforeUpload) {
    return beforeUpload(rawFile);
  } else {
    return true;
  }
}

/**
 * el-upload文件移除成功回调
 * @param uploadFile 移除的文件
 * @param uploadFiles 移除后的文件集合
 */
function uploadRemove(uploadFile: UploadFile, uploadFiles: UploadFiles): void {
  // 移除文件后更新model
  model.value = uploadFiles;
  // 校验表单
  validateFormItem(uploadFiles);
  // 如果外部监听了onRemove事件，则触发该事件
  const onRemove = contentAttrs.value['on-remove'] ?? contentAttrs.value['onRemove'];
  if (onRemove) {
    onRemove(uploadFile, uploadFiles);
  }
}

/**
 * 将el-upload选取的文件数组转换成后端需要的格式
 * @param uploadFiles
 */
function uploadFiles2Urls(uploadFiles: UploadFile[]): string {
  return uploadFiles
    .reduce((urls, file) => {
      if (file.status === 'success') {
        if (file.relativeUrl) {
          urls.push(file.relativeUrl);
        } else if (file.response) {
          const url = handleFileResponse?.(file.response);
          if (url) {
            urls.push(url);
          }
        }
      }
      return urls;
    }, [] as string[])
    .join(',');
}

type UploadFile = ElUploadFile & {
  relativeUrl?: string;
};

/**
 * 将后端返回的格式转换成el-upload需要的文件数组
 * @param urls
 */
function urls2UploadFiles(urls: string): UploadFiles {
  const relativeUrls = urls ? urls.split(',') : [];
  const files: UploadFile[] = [];
  relativeUrls.map(relativeUrl => {
    const file: UploadFile | undefined = fileList.value?.find(item =>
      item.response ? handleFileResponse?.(item.response) === relativeUrl : false,
    );
    if (!file) {
      // 之前没有选取过，手动创建一个file对象
      files.push({
        name: getFileName(relativeUrl),
        url: handleFileUrls?.(relativeUrl)[0],
        relativeUrl: relativeUrl,
        status: 'success',
        uid: genFileId(),
      });
    } else {
      files.push(file);
    }
  });
  return files;
}

/* location-picker */

/**
 * 地图选点弹窗是否显示
 */
const locationPickerDialog = ref(false);

/**
 * 地图确认选点回调
 */
function locationPickerConfirm() {
  locationPickerDialog.value = false;
  validateFormItem();
}

/**
 * 选中的位置地址
 */
const address = ref('');

/**
 * 监听AMap实例是否完成初始化
 */
watch(AMapRef, () => {
  if (
    props.fieldType === 'location' &&
    !address.value &&
    AMapRef.value &&
    model.value?.[0] &&
    model.value?.[1]
  ) {
    AMapRef.value.plugin(['AMap.Geocoder'], () => {
      const geocoder = new AMapRef.value.Geocoder({
        radius: 1000,
        extensions: 'all',
      });
      geocoder?.getAddress(model.value, (status: string, result: any) => {
        if (status === 'complete' && result.info === 'OK') {
          address.value = result.regeocode.formattedAddress;
        } else {
          console.error('[liv-web/form-item]逆地理编码失败！', result);
        }
      });
    });
  }
});

/* verify */

/**
 * 是否显示验证码组件
 */
const verifyVisible = ref(false);

/**
 * 加密表单项状态
 */
const ciphertextStatus = inject<Ref<{ [key: string]: boolean }> | undefined>(
  CIPHERTEXT_STATUS_KEY,
  undefined,
);

/**
 * 密文是否已解密
 */
const ciphertextDecrypted = computed(() => {
  return ciphertextStatus?.value?.[formItemAttrs.value.prop as string];
});

/**
 * 是否通过输入框更新绑定值
 */
const updateFromInput = ref(false);

/**
 * 监听加解密表单项的绑定值
 * @remarks 表单编辑场景下，绑定值的初始值可能为空，需要调用详情接口并进行赋值，
 * 此时需要将表单置为加密状态，并更新初始值
 */
watch(model, newValue => {
  if (props.fieldType === 'ciphertext') {
    if (ciphertextStatus?.value) {
      if (updateFromInput.value) {
        // 用户手动输入更新绑定值，不更新解密状态
        updateFromInput.value = false;
      } else {
        if (newValue !== undefined && newValue !== null && newValue !== '') {
          // 通过赋值更新绑定值，将表单置为加密状态
          ciphertextStatus.value[formItemAttrs.value.prop as string] = false;
          initialVal.value = model.value;
        }
      }
    }
  }
});

/**
 * 加解密输入框绑定值更新回调（不触发加解密状态的改变）
 * @param e 输入的值
 */
function ciphertextUpdate(e: string) {
  updateFromInput.value = true;
  model.value = e;
}

/**
 * 验证码校验成功回调
 * @param params 校验结果
 */
function verifySuccess(params: VerifyResult) {
  const decryptImpl = contentAttrs.value.decrypt?.(params, formModel?.value);
  if (decryptImpl) {
    if (decryptImpl instanceof Promise) {
      decryptImpl.then(res => {
        ciphertextUpdate(res);
        if (ciphertextStatus?.value) {
          ciphertextStatus.value[formItemAttrs.value.prop as string] = true;
        }
      });
    } else if (typeof decryptImpl === 'string') {
      ciphertextUpdate(decryptImpl);
      if (ciphertextStatus?.value) {
        ciphertextStatus.value[formItemAttrs.value.prop as string] = true;
      }
    } else {
      console.error('[liv-web/form-item]请检查传入的解密逻辑是否正确！');
    }
  } else {
    console.error('[liv-web/form-item]当fieldType为ciphertext时，需要传入具体的解密逻辑！');
  }
}

/**
 * 重置加密表单项
 */
function resetCiphertext() {
  model.value = initialVal.value;
  if (ciphertextStatus?.value) {
    // 重置字段解密状态
    ciphertextStatus.value[formItemAttrs.value.prop as string] = false;
  }
}

onMounted(() => {
  // fieldType为numberRange时，重写表单的重置字段方法
  if (props.fieldType === 'numberRange' && Array.isArray(formItemAttrs.value.prop)) {
    initialVal.value = clone(model.value);
    function resetNumberRange() {
      model.value = [initialVal.value[0], initialVal.value[1]];
      if (formContext?.model) {
        if (formItemAttrs.value.prop) {
          formContext.model[formItemAttrs.value.prop[0]] = initialVal.value[0];
          formContext.model[formItemAttrs.value.prop[1]] = initialVal.value[1];
        }
      }
    }
    // @ts-ignore
    const field = formContext?.getField?.(formItemAttrs.value.prop);
    if (field) {
      field.resetField = resetNumberRange;
    }
  }
  // fieldType为location时，重写表单的重置字段方法
  if (props.fieldType === 'location' && Array.isArray(formItemAttrs.value.prop)) {
    initialVal.value = clone(model.value);
    function resetLocation() {
      model.value = [initialVal.value[0], initialVal.value[1]];
      if (formContext?.model) {
        if (formItemAttrs.value.prop) {
          formContext.model[formItemAttrs.value.prop[0]] = initialVal.value[0];
          formContext.model[formItemAttrs.value.prop[1]] = initialVal.value[1];
        }
      }
    }
    // @ts-ignore
    const field = formContext?.getField?.(formItemAttrs.value.prop);
    if (field) {
      field.resetField = resetLocation;
    }
  }
  // fieldType为ciphertext时，重写表单的重置字段方法
  if (props.fieldType === 'ciphertext') {
    initialVal.value = model.value;
    const field = formContext?.getField?.(formItemAttrs.value.prop as string);
    if (field) {
      field.resetField = resetCiphertext;
    }
  }
});
</script>
